<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-lx-calendar"></i> 
        </el-breadcrumb-item>
        <el-breadcrumb-item>DualWMDR</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="introduction-box">
        <el-row :gutter="20" >
          <el-col :span="9">
            <el-collapse v-model="activeName" accordion>
              <el-collapse-item title=" Algorithm introduction" name="1">
                <div></div>
                <div></div>
              </el-collapse-item>
              <el-collapse-item title=" Algorithm flowchart" name="2">
                <el-image :src="pics"></el-image>
              </el-collapse-item>
              <el-collapse-item title=" Parameter Description" name="3">
                <div></div>
                <div></div>
                <div></div>
              </el-collapse-item>
              <el-collapse-item title=" Data sample" name="4">
                <div></div>
                <div></div>
              </el-collapse-item>
            </el-collapse>
          </el-col>
          <el-col :span="6">
            <el-form :model="params" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
              <el-form-item label="percent:" prop="percent">
                <el-input v-model="params.percent" ></el-input>
              </el-form-item>
              <el-form-item label="topT:" prop="topT">
                <el-input v-model="params.topT" ></el-input>
              </el-form-item>
              <el-form-item label="topK:" prop="topK">
                <el-input v-model="params.topK" ></el-input>
              </el-form-item>
              <el-form-item label="threshold:" prop="threshold">
                <el-input v-model="params.threshold"></el-input>
              </el-form-item>
              <el-form-item label="" prop="data_1">
                <el-upload
                  class="upload-demo"
                  ref="my-upload"
                  action="http://localhost:8080/DualWMDRInputDataUpload"
                  name="txtFile"
                  :show-file-list = false
                  :on-preview="handlePreview"
                  :on-remove="handleRemove"
                  :before-remove="beforeRemove"
                  multiple
                  :limit="105"
                  :on-success="uploadSuccess"
                  :on-exceed="handleExceed">
                  <el-button type="primary" :disabled = !frontParams.readyRun></el-button>
                  <div slot="tip" class="el-upload__tip">txt500MB</div>
                </el-upload>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="6" >
            <el-form :model="params" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
              <el-form-item label="fold:" prop="fold">
                <el-input v-model="params.fold"></el-input>
              </el-form-item>
              <el-form-item label="constant:" prop="constant">
                <el-input v-model="params.constant"></el-input>
              </el-form-item>
              <el-form-item label="alpha:" prop="alpha">
                <el-input v-model="params.alpha"></el-input>
              </el-form-item>
              <el-form-item label="order:" prop="order">
                <el-input v-model="params.order"></el-input>
              </el-form-item>
              <el-form-item>
                <p></p>
              </el-form-item>
              <el-form-item>
                <p></p>
              </el-form-item>
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span></span>
                </div>
                <div class="text item">
                  <el-progress :text-inside="true" :stroke-width="24" :percentage=paramId.progress status="success"></el-progress>
                </div>
              </el-card>
              <el-form-item>
                <p></p>
              </el-form-item>
            </el-form>
            <el-row type="flex" justify="left">
              <el-button type="primary" @click="submitForm()" :disabled = !frontParams.readyRun></el-button>
              <el-button type="primary" @click="downloadRes()" :disabled = !paramId.finished plain></el-button>
              <!--              <el-button @click="stopPoll()"></el-button>-->
            </el-row>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>

import {DualWMDRParamsUpload, DualWMDRPollResultData} from '../../api/index'

export default {
  name: 'DualWMDRPage',
  data () {
    return {
      // 
      activeName: '1',
      pics: 'http://img01.jituwang.com/200714/175123-200G405012197.jpg',
      // 
      params: {
        percent: '1.0',
        topT: '200',
        topK: '100',
        threshold: '1',
        fold: '5',
        constant: '0.5',
        alpha: '0.25',
        order: '2'
      },
      paramId: {
        queryId: '',
        finished: false,
        progress: 0
      },
      frontParams: {
        readyRun: true
      },
      localParams: {
        fileCount: 0
      },
      rules: {
        percent: [
          { required: true, message: 'percent', trigger: 'blur' },
          { min: 1, max: 6, message: ' 1  2 ', trigger: 'blur' }
        ],
        topT: [
          { required: true, message: 'topT', trigger: 'change' }
        ],
        topK: [
          { required: true, message: 'topK', trigger: 'change' }
        ],
        threshold: [
          { required: true, message: 'threshold', trigger: 'change' }
        ],
        fold: [
          { required: true, message: 'fold', trigger: 'change' }
        ],
        constant: [
          { required: true, message: 'constant', trigger: 'change' }
        ],
        alpha: [
          { required: true, message: 'alpha', trigger: 'change' }
        ],
        order: [
          { required: true, message: 'order', trigger: 'change' }
        ]
      }
    }
  },

  methods: {
    // 
    submitForm () {
      // 
      DualWMDRParamsUpload(this.params).then(res => {
        console.log(res)
        console.log(res.queryId)
        this.paramId.queryId = res.queryId
      })
      this.startPoll()
      // 
      this.frontParams.readyRun = false
      this.paramId.finished = false
      // 
      this.wipeButt()
    },
    // 
    downloadRes () {
      console.log('downloadRes Method')
      window.location.href = 'http://localhost:8080/DualWMDRResultDataDownload'
      // 
      this.frontParams.readyRun = true
    },
    // 
    pollResultData () {
      DualWMDRPollResultData(this.paramId).then(res => {
        console.log(res)
        if (res.finished === 'true') {
          // do something
          this.paramId.finished = true
          this.stopPoll()
        }
        // 
        this.paramId.progress = res.progress
      })
    },
    startPoll () {
      this.myInterval = window.setInterval(() => {
        setTimeout(() => {
          this.pollResultData()
        }, 1)
      }, 3000)
    },
    // 
    stopPoll () {
      clearInterval(this.myInterval)
    },
    // 
    createQueryId () {
      var _this = this
      let yy = new Date().getFullYear()
      let mm = new Date().getMonth() + 1
      let dd = new Date().getDate()
      let hh = new Date().getHours()
      let mf = new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes()
      let ss = new Date().getSeconds() < 10 ? '0' + new Date().getSeconds() : new Date().getSeconds()
      let getTime = yy + '-' + mm + '-' + dd + '-' + hh + '-' + mf + '-' + ss
      _this.params.queryId = getTime
      console.log(this.params.queryId)
    },
    // 
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    handlePreview (file) {
      console.log(file)
    },
    handleExceed (files, fileList) {
      this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `)
    },
    beforeRemove (file, fileList) {
      return this.$confirm(``)
    },
    uploadSuccess (response, files, fileList) {
      this.localParams.fileCount += 1
      if (this.localParams.fileCount < fileList.length) {
        return 0
      } else {
        return this.$message({
          message: `${fileList.length}`,
          type: `success`
        })
      }
    },
    // fileCount
    wipeButt () {
      this.localParams.fileCount = 0
      this.$refs['my-upload'].clearFiles()
    },
    // 
    uploadFiles () {
      this.paramId.progress = 0
    }
  }
}
</script>

<style>
.introduction-box{
  height: 500px;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0;
}
.left-bottom{
  margin-top: 20px;
}
.right-bottom{
  margin-top: 20px;
}
.el-upload--picture-card{
  width: 100px;
  height: 100px;
}
.el-upload{
  width: 100px;
  height: 100px;
  line-height: 100px;
}
.el-upload-list--picture-card .el-upload-list__item{
  width: 100px;
  height: 100px;
  line-height: 100px;
}
.el-upload-list--picture-card .el-upload-list__item-thumbnail{
  width: 100px;
  height: 100px;
  line-height: 100px;
}
.avatar{
  width: 100px;
  height: 100px;
}
</style>



